/* =========== STARTbase remove kpbs stlye.css elements ===========*/
body {
  --topBarHeight: 0;
  --navBarHeight: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  /* regular */
}
.container {
  max-width: 940px;
  margin: 0 auto;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  /* bold */
}
/* =========== END base remove kpbs stlye.css elements ===========*/
/* 
=====================================================================
button block
=====================================================================
*/
.arrow-btn {
  border-radius: 50px;
  background-clip: padding-box;
  font-size: 1.125em;
  display: inline-block;
  font-weight: 500;
}
.arrow-btn svg {
  width: 20px;
  margin-left: 5px;
}
.arrow-btn.white-arrow-btn {
  background-color: #fff;
  color: #2638C4;
  padding: 10px 25px;
}
/* 
=====================================================================
Hero Block
=====================================================================
*/
.passport-hero {
  background-color: #0B135C;
  min-height: 440px;
  display: flex;
  flex-wrap: nowrap;
  color: #fff;
}
.passport-hero__content {
  padding: 60px 50px;
}
.passport-hero__content__logo svg {
  margin-bottom: 25px;
}
.passport-hero__content__cta {
  margin-bottom: 25px;
}
.passport-hero__content p {
  margin-top: 0;
}
.passport-hero__image {
  width: 100%;
  background-position: initial;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../img/passport_image.jpg);
  height: 440px;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.25);
}
.passport-hero__image__circle {
  width: 350px;
  background: radial-gradient(55em 60em at -97% 50%, #0B135C 50%, transparent 50%);
  height: 440px;
}
